<script>
export default {

  data() {
    return {
      first_name: '',
    };
  },
  methods: {
    addOne() {
      this.$emit('addOne', this.first_name);
    },
  },
};
</script>

<template>
<div>
  <input type="text" v-model="first_name" placeholder="prenom">
  <button v-on:click="addOne">Ajouter</button>
</div>
</template>

<style>

</style>



